.listWrapper {
  position: relative;
}
.listItem {
  position: relative;
  z-index: 100000;
}
.listItem:not(:last-child) {
  position: absolute;
  width: 100%;
  top: 0;
  left: 0;
}
.listItem:nth-last-child(2) {
  position: absolute;
  width: 100%;
  top: 0;
  left: 0;
}
.listItem:not(:first-child) {
  margin-top: 1em;
}
