.dropzone {
  display: flex;
  text-align: center;
  justify-content: center;
  align-items: center;
  background: var(--c-dropzone-bg, #e8f0feb3) 0 0;
  border: 1px solid var(--c-primary, #78a9fe);
  position: sticky;
  width: 100%;
  height: 100%;
  z-index: 1;
  top: 0;
  left: 0;
}
.dropzoneDisabled {
  display: none;
}
.dropzoneTitle {
  font-size: 24px;
  line-height: 1;
  font-weight: bold;
  margin: 0;
}
.dropzoneSubtitle {
  margin: 16px 0 0;
  font-size: 16px;
  font-weight: bold;
}
