.plainMessage {
  white-space: normal;
  word-wrap: break-word;
  word-break: break-word;
}
.sentiment,
.sentimentLeft,
.sentimentRight {
  position: absolute;
  top: 0;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  opacity: 0;
  visibility: hidden;
  transition: all 0.3s ease;
}
.sentiment.visible,
.sentimentLeft.visible,
.sentimentRight.visible {
  opacity: 1;
  visibility: visible;
}
.sentimentLeft {
  left: 0;
  transform: translateX(-100%);
  margin-left: -5px;
}
.sentimentRight {
  right: 0;
  margin-right: -8px;
  top: 50%;
  transform: translate(100%, -50%);
}
