.remoteAssistMessage {
  margin-right: 6px;
  align-self: flex-end;
  max-width: 71%;
  padding: 8px;
  border: 1px solid var(--c-chat-border, #3c3c4333);
  border-radius: 4px;
  font-size: 14px;
  line-height: 17px;
  letter-spacing: -0.34px;
}
.remoteAssistMessageHeader {
  display: flex;
  flex-flow: row nowrap;
}
.remoteAssistMessageIcon {
  height: 16px;
  width: 16px;
  margin-right: 8px;
}
.remoteAssistMessageContent {
  display: flex;
  flex-flow: column nowrap;
  margin-left: 24px;
  margin-top: 4px;
  overflow: hidden;
}
.remoteAssistMessageContent .remoteAssistContentRow {
  margin-bottom: 4px;
  display: flex;
}
.remoteAssistMessageContent .remoteAssistContentRow > span:first-child {
  color: var(--c-main-text-secondary, #3c3c4399);
  min-width: 90px;
  margin-right: 16px;
}
.remoteAssistMessageContent .remoteAssistContentRow > span:last-child {
  min-width: 46px;
}
