.root {
  height: 100%;
  display: flex;
  flex-direction: column;
}
.list {
  padding: 0 8px 0 16px;
  margin-right: 6px;
  flex: 1 1 auto;
  overflow: hidden !important;
  overflow-y: auto !important;
  position: relative;
}
.selectionContainer {
  margin-top: 4px;
  height: 0;
  display: flex;
  flex-direction: column;
  flex: 1 1 auto;
}
.filters,
.filtersRight {
  display: flex;
  align-items: center;
}
.filters {
  padding: 8px 16px 14px 16px;
}
.searchInput {
  margin-right: 12px;
}
.filterButton:global(.ui.button) {
  margin: 0 0 0 4px;
  width: 36px;
  height: 36px;
  padding: 0;
  display: flex;
  align-items: center;
  justify-content: center;
}
.mark {
  background-color: var(--c-primary, #2185d0);
  color: var(--c-primary-contrast, #fff);
  opacity: 0.5;
}
.mark.highlighted {
  opacity: 1;
}
