.messageRow {
  display: flex;
  padding-bottom: 8px;
  align-items: center;
}
.messageRow.incoming {
  padding-right: calc(58px + 4px);
}
.messageRow.outgoing {
  flex-direction: row-reverse;
  padding-left: calc(58px + 4px);
}
.time {
  color: var(--c-main-text-secondary, #767676);
  font-size: 12px;
  flex-shrink: 0;
  user-select: none;
  width: 58px;
  letter-spacing: 0;
}
.time.incoming {
  margin-left: 8px;
}
.time.outgoing {
  margin-left: 8px;
}
.message {
  align-items: center;
  padding: 8px 4px;
  border-radius: 4px;
  max-width: 60%;
}
.message.incoming {
  background: #f8f9f9;
}
.message.outgoing {
  border: 1px solid var(--c-main-border, #e2e2e3);
  background: white;
}
.message.link {
  background: var(--c-voice-transcript-link-bg, #fcfff5);
  border: 1px solid var(--c-voice-transcript-link-border, #a8c599);
  color: var(--c-voice-transcript-link-text, #1e561fcc);
  max-width: 100%;
}
.message.link a {
  color: var(--c-voice-transcript-link-text, #1e561fcc);
  text-decoration: underline;
}
.message.link a:hover {
  text-decoration: none;
}
.message.error {
  border: 1px solid var(--c-danger-bg, red);
}
.message.html {
  background: var(--c-voice-transcript-html-bg, #e8ebfa);
  border: 1px solid var(--c-voice-transcript-html-border, #a8c599);
  max-width: 100%;
}
.message.closable {
  display: flex;
  justify-content: space-between;
}
.sentiment {
  width: 18px;
  height: 18px;
  user-select: none;
}
.sentiment.incoming {
  margin-left: 8px;
}
.sentiment.outgoing {
  margin-right: 8px;
}
.icon {
  align-self: flex-start;
  user-select: none;
}
.icon.incoming {
  margin-left: 4px;
}
.icon.outgoing {
  margin-right: 4px;
}
