.file {
  padding: 8px 10px;
}
.file.isError {
  background: var(--c-error-bg, #ffdee2);
  color: var(--c-error-text, #ff8997);
}
.file.isError:before,
.file.isError:after {
  display: none;
}
.maskFilePopup {
  padding: 0 !important;
}
.maskedFileImageWrapper,
.maskedAttachment {
  position: relative;
}
.maskedFileImageWrapper:after,
.maskedAttachment:after {
  background: rgba(0, 0, 0, 0.3);
  border-radius: 4px;
  content: '';
  position: absolute;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
}
.maskedAttachment {
  border-radius: 8px;
  border: none;
}
.selectionPopupOption {
  position: relative;
  z-index: 2;
  padding: 4px 16px;
  cursor: pointer;
  border: none;
  background-color: #fff;
  line-height: 1.4285em;
  width: 100%;
}
.selectionPopupOption:hover {
  background-color: rgba(0, 0, 0, 0.04);
}
.voice {
  max-width: 80%;
}
