.noMessages {
  position: relative;
  top: 55px;
  left: 50%;
  transform: translateX(-50%);
  font-size: 14px;
  text-align: center;
}
.noRecent {
  margin: 0;
  line-height: 28px;
}
.searchForOlder {
  color: var(--c-link, #538ec0);
  cursor: pointer;
  line-height: 28px;
  border: none;
  background-color: transparent;
  transition: all 0.3s ease;
}
.searchForOlder:hover {
  color: var(--c-link-hover, #538ec0);
}
