.messagesContainer {
  height: 100%;
}
.messages {
  padding: 0 8px 0 16px;
  margin-right: 6px;
  flex: 1 0 auto;
  height: 100%;
  overflow: hidden !important;
  overflow-y: auto !important;
  position: relative;
}
:global(.bp-masked) {
  border: 1px solid var(--neutral-200);
  border-radius: 2px;
  background: rgba(0, 0, 0, 0.04);
  cursor: pointer;
}
:global(.bp-masked) a {
  pointer-events: none;
}
