.messageWrapper {
  display: flex;
  flex-direction: column;
  flex: 1 1 auto;
  z-index: 0;
}
.messageTopInfo {
  font-size: 11px;
  line-height: 16px;
  letter-spacing: 0;
  color: #8e8e93;
  margin: 0 16px 4px 0;
  display: flex;
  justify-content: flex-end;
  text-align: right;
}
.messageTopInfo > * {
  user-select: none;
}
.messageTopInfo.incoming {
  flex-flow: row-reverse;
  padding-left: 16px;
}
.messageBlock {
  display: flex;
  align-items: flex-end;
  padding-bottom: 10px;
  position: relative;
  z-index: 2;
}
.messageBlock.outgoing {
  justify-content: flex-end;
}
.messageBlock.outgoing .avatar {
  margin-left: 3px;
}
.messageBlock.outgoing .avatar.placementTop {
  align-self: flex-start;
  margin-top: 20px;
}
.messageBlock.outgoing .avatar.remoteAssistMessageAvatar {
  margin-left: 0;
  margin-bottom: auto;
  margin-top: 23px;
}
.messageBlock.incoming {
  justify-content: flex-start;
}
.messageBlock.incoming .avatar {
  margin-right: 8px;
  margin-left: 4px;
}
.message {
  display: flex;
  width: 100%;
  align-items: flex-end;
}
.message.urgent {
  color: theme('colors.red.500');
}
.message.outgoing {
  padding-right: 6px;
  flex-direction: row-reverse;
}
.message.incoming {
  padding-left: 6px;
}
.message.incoming > img {
  margin-right: 7px;
}
.message.incoming > .messageContent:before {
  bottom: -2px;
  left: -7px;
  right: auto;
  border-left: 20px solid var(--c-chat-bg, #e9e9eb);
  border-bottom-right-radius: 16px 14px;
  border-right: none;
  border-bottom-left-radius: 0;
}
.message.incoming > .messageContent:after {
  left: 19px;
  right: auto;
  border-bottom-right-radius: 10px;
  border-bottom-left-radius: 0;
}
.messageContent {
  background: var(--c-chat-bg, #e9e9eb);
  border: solid 1px var(--c-chat-bg, #e9e9eb);
  border-radius: 16px;
  padding: 5px 7px;
  max-width: 60%;
  display: inline-block;
  position: relative;
  word-wrap: break-word;
  word-break: break-word;
}
.messageContent:before {
  content: '';
  position: absolute;
  z-index: -1;
  bottom: -2px;
  right: -7px;
  left: auto;
  height: 20px;
  border-right: 20px solid var(--c-chat-bg, #e9e9eb);
  border-bottom-left-radius: 16px 14px;
  transform: translate(0, -2px);
}
.messageContent:after {
  content: '';
  position: absolute;
  z-index: -1;
  bottom: -2px;
  left: auto;
  right: -41px;
  width: 11px;
  height: 21px;
  background: var(--c-main-bg, #fff);
  border-bottom-left-radius: 10px;
  transform: translate(-30px, -2px);
}
.messageContentHtml table {
  border-collapse: collapse;
}
.messageContentHtml table td {
  white-space: initial;
  border: 1px solid gray;
}
.messageContentHtml blockquote {
  white-space: initial;
  position: relative;
  margin: 0.5em 0;
  padding: 0.5em 12px;
}
.messageContentHtml blockquote:before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  width: 2px;
  background: #2185d0;
}
.messageContent.voice {
  flex: 0 0 auto;
  padding: 0;
  border: none;
  background: transparent;
  max-width: 80%;
}
.messageContent.voiceRecording {
  min-width: 300px;
}
.messageContentHtml.emi p {
  margin: 0 !important;
}
.avatar {
  display: flex;
  font-size: 24px;
  border-radius: 50%;
  z-index: 4;
  user-select: none;
}
.errorMessageContent {
  background: var(--c-error-bg, #fff6f6);
  color: var(--c-error-text, #9f3a38);
}
.errorMessageContent:before {
  border-right-color: var(--c-error-bg, #fff6f6);
}
.messageTopInfoGap {
  width: 8px;
}
.transcriptSuggestionAvatar {
  display: inline-block;
  width: 17px;
  height: 17px;
  margin-top: -3px;
  vertical-align: middle;
  margin-right: 5px;
}
.timeChannel {
  display: flex;
  align-items: center;
  flex-direction: row;
}
.timeChannel .channelIcon {
  fill: var(--c-icon, #767676);
  margin-right: 8px;
}
.timeChannel.incoming .channelIcon {
  margin-left: -8px;
}
.selectionPopupOption {
  display: block;
  z-index: 2;
  padding: 4px 16px;
  cursor: pointer;
  border: none;
  background-color: #fff;
  line-height: 1.4285em;
  width: 100%;
}
.selectionPopupOption:hover {
  background-color: rgba(0, 0, 0, 0.04);
}
